import React from 'react'
import './Button.scss'
import { Link } from 'react-router-dom'

interface IProps {
  children?: React.ReactNode
  to?: string
  className?: string
  color?: '_orange' | '_green'
  onClick?: () => void
}

const Button = ({
  children,
  to,
  className,
  color = '_orange',
  onClick,
  ...props
}: IProps) => {
  return (
    <>
      {to ? (
        <Link
          className={`button ${className ? className : ''} ${color} ${
            className ? className : ''
          }`}
          {...props}
          to={to}
        >
          {children}
        </Link>
      ) : (
        <button
          className={`button ${className ? className : ''} ${color} ${
            className ? className : ''
          }`}
          onClick={onClick}
          {...props}
        >
          {children}
        </button>
      )}
    </>
  )
}

export default Button
