import React, { FC } from 'react'
import {
  BaseNew,
  BaseSvg,
  ButtonArrowRight,
  ButtonSlider,
  Container,
} from '../../index'
import './Clubs.scss'
import { Swiper, SwiperSlide } from 'swiper/react'
import SwiperCore, { Navigation } from 'swiper'
import { Link } from 'react-router-dom'
import { IClubs } from '../../../models/Clubs'

interface IProps {
  clubs: IClubs[]
}

SwiperCore.use([Navigation])

const Clubs: FC<IProps> = (props) => {
  return (
    <section className="home-clubs">
      <Container>
        <div className="home-clubs__body">
          <div className="home-clubs__info">
            <h2 className="home-clubs__title">уже 20 спортзалов в Харькове</h2>
            <p className="home-clubs__subtitle">
              Мы делаем город здоровее, а людей успешнее, создавая здоровую
              среду для движения вперед!
            </p>
            <ButtonArrowRight to="/clubs">смотреть все</ButtonArrowRight>
          </div>
          <Swiper
            className="home-clubs__slider"
            slidesPerView="auto"
            navigation={{
              nextEl: '.home-clubs__btn-next',
              prevEl: '.home-clubs__btn-prev',
            }}
          >
            {props.clubs.map((item: IClubs) => (
              <SwiperSlide className="home-clubs__slide" key={item.id}>
                <Link
                  to={`/clubs/${item.slug}`}
                  className="home-clubs__slide-link"
                >
                  <img
                    className="home-clubs__slide-img"
                    src={item.image}
                    alt=""
                  />
                  {item.new && <BaseNew className="home-clubs__slide-new" />}
                  <div className="home-clubs__slide-content">
                    <BaseSvg
                      className="home-clubs__slide-logo"
                      name="ico-logo-text"
                    />
                    <div className="home-clubs__slide-title">{item.name}</div>
                  </div>
                </Link>
              </SwiperSlide>
            ))}
            <ButtonSlider
              type="next"
              className="home-clubs__btn home-clubs__btn-next d-none d-md-flex"
            />
            <ButtonSlider
              type="prev"
              className="home-clubs__btn home-clubs__btn-prev d-none d-md-flex"
            />
            <ButtonSlider
              type="next"
              className="home-clubs__btn home-clubs__btn-next d-md-none _desktop"
            />
          </Swiper>
        </div>
      </Container>
    </section>
  )
}

export default Clubs
