import React, { FC, useContext } from 'react'
import './ClubScheduleTable.scss'
import { ScheduleContext } from '../../../pages/Club'
import { IScheduleItem } from '../../../models/Clubs'

const ClubScheduleTable: FC = () => {
  const schedule = useContext(ScheduleContext)

  return (
    <div className="schedule-table">
      <div className="schedule-table__top schedule-table__row">
        <div />
        <div className="schedule-table__top-item">направление</div>
        <div className="schedule-table__top-item">Пн</div>
        <div className="schedule-table__top-item">вт</div>
        <div className="schedule-table__top-item">ср</div>
        <div className="schedule-table__top-item">чт</div>
        <div className="schedule-table__top-item">пт</div>
        <div className="schedule-table__top-item">сб</div>
      </div>

      {schedule &&
        Object.keys(schedule).map((item, index) => (
          <li key={index} className="schedule-table__row schedule-table__item">
            {schedule[item] &&
              Object.keys(schedule[item]).map((element) => {
                schedule[item][element].some(
                  (scheduleItem: IScheduleItem) => scheduleItem?.recruiting
                )
              }) && (
                <div className="schedule-table__item-recruiting">
                  идет набор
                </div>
              )}
            <div className="schedule-table__item-title schedule-table__item-item">
              {item}
            </div>
            {schedule[item] &&
              Object.keys(schedule[item]).map((element, index) => (
                <div key={index} className="schedule-table__item-item _start">
                  {schedule[item][element].map(
                    (scheduleItem: IScheduleItem, index) => (
                      <span key={index}>{scheduleItem.from}</span>
                    )
                  )}
                </div>
              ))}
          </li>
        ))}
    </div>
  )
}

export default ClubScheduleTable
