import windowDimensions from '../hooks/windowDimensions'
import { useEffect, useState } from 'react'

interface IBasicStyleModal {
  overlay?: IOverlay
  content?: IContent
}

interface IOverlay {
  backgroundColor?: string
  zIndex?: number
  display?: string
  flexDirection?: string
  alignItems?: string
  justifyContent?: string
}

interface IContent {
  backgroundColor?: string
  zIndex?: number
  display?: string
  flexDirection?: string
  alignItems?: string
  borderRadius?: string
}

export default function useBasicStyleModal() {
  const overlayBase = {
    backgroundColor: 'rgba(7,7,19,0.25)',
    zIndex: 99,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  }

  const baseContent = {
    position: 'static',
    boxShadow: '0 6.5rem 10rem rgba(7, 7, 19, 0.1)',
    padding: 0,
    border: 'none',
    color: 'inherit',
    backgroundColor: 'transparent',
  }
  const { windowWidth, md } = windowDimensions()
  const [basicStyleModal, setBasicStyleModal] = useState<IBasicStyleModal>({})

  useEffect(() => {
    if (windowWidth <= md) {
      setBasicStyleModal({
        ...basicStyleModal,
        content: {
          borderRadius: '2.5rem 2.5rem 0 0',
          ...baseContent,
        },
        overlay: {
          justifyContent: 'flex-end',
          ...overlayBase,
        },
      })
    } else {
      setBasicStyleModal({
        ...basicStyleModal,
        content: {
          borderRadius: '4.5rem',
          ...baseContent,
        },
        overlay: {
          justifyContent: 'center',
          ...overlayBase,
        },
      })
    }
  }, [windowWidth])

  return {
    basicStyleModal,
  }
}
