import React, { useEffect, useState } from 'react'
import Promo from '../../components/Promo/Promo'
import { BaseSvg, Container, Map } from '../../components'
import BreadCrumbs from '../../components/BreadCrumbs/BreadCrumbs'
import { IBreadCrumbs } from '../../models/BreadCrumbs'
import '../../assets/scss/_utils.scss'
import './Contacts.scss'
import TickerReact from '../../components/TickerReact/TickerReact'
import { useAppDispatch, useAppSelector } from '../../hooks/redux'
import { RootState } from '../../store'
import { fetchClubs } from '../../store/slices/сlubs'
import { IClubs } from '../../models/Clubs'
import { Link } from 'react-router-dom'

const Index = () => {
  const [breadCrumbs] = useState<IBreadCrumbs[]>([
    {
      id: 1,
      name: 'контакты',
    },
  ])
  const dispatch = useAppDispatch()
  const { clubs } = useAppSelector((state: RootState) => state.clubs)

  useEffect(() => {
    if (clubs && clubs.length) return
    dispatch(fetchClubs(1))
  }, [])

  return (
    <div className="contacts">
      <Promo title="контакты">
        <Link
          to="/clubs?tab=map"
          className="contacts__btn btn _violet _strong-italic"
        >
          <span className="d-md-none">смотреть все клубы на карте</span>
          <BaseSvg name="ico-arrow-right-long" className="d-md-none" />

          <span className="d-none d-md-inline-flex">на карте</span>
          <BaseSvg
            name="ico-map"
            className="contacts__btn-svg d-none d-md-inline-flex"
          />
        </Link>
      </Promo>
      <BreadCrumbs dataList={breadCrumbs} />
      <Container>
        <ul className="contacts__list">
          {clubs.length
            ? clubs.map((item: IClubs) => (
                <li key={item.id} className="contacts__item">
                  <div className="contacts__item-content">
                    <div className="contacts__item-title">
                      <span>pulse gym {item.name}</span>
                      <BaseSvg
                        name="ico-arrow-right-short"
                        className="contacts__item-title-svg"
                      />
                    </div>
                    <ul className="contacts__item-items">
                      <li className="contacts__item-item">
                        <span className="contacts__item-label">Адрес:</span>
                        <span className="contacts__item-value">
                          {item.address}
                        </span>
                      </li>
                      <li className="contacts__item-item">
                        <span className="contacts__item-label">
                          Режим работы:
                        </span>
                        <span className="contacts__item-value">
                          Пн-Сб с 8:00 до 22:00, Вс с 8:00 до 20:00
                        </span>
                      </li>
                      <li className="contacts__item-item">
                        <span className="contacts__item-label">Телефоны:</span>
                        <span className="contacts__item-value">
                          +38 050 465 1435, +38 073 465 1435
                        </span>
                      </li>
                    </ul>
                  </div>

                  <Map
                    markers={[
                      {
                        id: item.id,
                        title: item.name,
                        address: item.address,
                        position: {
                          lat: Number(item.lat),
                          lng: Number(item.lng),
                        },
                      },
                    ]}
                    className="contacts__item-map _small-fullscreen-btn d-md-none"
                  />
                </li>
              ))
            : null}
        </ul>
      </Container>
      <TickerReact className="_mb-2 d-md-none" />
    </div>
  )
}

export default Index
