import React from 'react'
import { BrowserRouter, Routes, Route } from 'react-router-dom'
import LayoutDefault from './layouts/Default'
import {
  Blog,
  Club,
  Clubs,
  Contacts,
  Guide,
  Home,
  OneBlog,
  Photo,
  Reviews,
  Trainer,
} from './pages'
import PhotoGallery from './pages/PhotoGallery'
import ClubPrice from './pages/ClubPrice/ClubPrice'
import ClubSchedulePage from './pages/ClubSchedulePage/ClubSchedulePage'
import Error from './pages/Error/Error'
import ScrollToTop from './components/ScrollToTop/ScrollToTop'
import Regulations from './pages/Regulations/Regulations'

const App = () => {
  return (
    <BrowserRouter>
      <ScrollToTop>
        <Routes>
          <Route path="/" element={<LayoutDefault />}>
            <Route index element={<Home />} />
            <Route path="/clubs" element={<Clubs />} />
            <Route path="/clubs/:id" element={<Club />} />
            <Route path="/clubs-price/:id" element={<ClubPrice />} />
            <Route path="/clubs-schedule/:id" element={<ClubSchedulePage />} />
            <Route path="/trainer/:id/:slug" element={<Trainer />} />
            <Route path="/blog" element={<Blog />} />
            <Route path="/blog/:id" element={<OneBlog />} />
            <Route path="/reviews" element={<Reviews />} />
            <Route path="/contacts" element={<Contacts />} />
            <Route path="/photo" element={<Photo />} />
            <Route path="/photo/:id" element={<PhotoGallery />} />
            <Route path="/regulations/:id" element={<Regulations />} />
            <Route path="*" element={<Error />} />
            {process.env.NODE_ENV === 'development' && (
              <Route path="/guide" element={<Guide />} />
            )}
          </Route>
        </Routes>
      </ScrollToTop>
    </BrowserRouter>
  )
}

export default App
