import React, { FC, useEffect, useState } from 'react'
import Promo from '../../components/Promo/Promo'
import BreadCrumbs from '../../components/BreadCrumbs/BreadCrumbs'
import { IBreadCrumbs } from '../../models/BreadCrumbs'
import { BaseCheckbox, ClubsItems, HomeMap } from '../../components'
import './Clubs.scss'
import TickerReact from '../../components/TickerReact/TickerReact'
import { useAppDispatch, useAppSelector } from '../../hooks/redux'
import { fetchClubs } from '../../store/slices/сlubs'
import { RootState } from '../../store'
import { IClubs } from '../../models/Clubs'
import { useSearchParams } from 'react-router-dom'

const Clubs: FC = () => {
  const [breadCrumbs] = useState<IBreadCrumbs[]>([
    {
      id: 1,
      name: 'Наши клубы',
    },
  ])
  const dispatch = useAppDispatch()
  const [searchParams] = useSearchParams()
  const { clubs } = useAppSelector((state: RootState) => state.clubs)
  const [markers, setMarkers] = useState([])

  useEffect(() => {
    if (clubs && clubs.length) return
    dispatch(fetchClubs(1))
  }, [])

  useEffect(() => {
    if (!clubs.length) return
    const result: any = []
    clubs.forEach((el: IClubs) => {
      result.push({
        id: el.id,
        title: el.name,
        address: el.address,
        position: {
          lat: +el.lat,
          lng: +el.lng,
        },
      })
    })
    setMarkers(result)
  }, [clubs])

  return (
    <div className="clubs-page">
      <Promo title="НАШИ КЛУБЫ">
        <BaseCheckbox />
      </Promo>
      <BreadCrumbs dataList={breadCrumbs} />
      {clubs.length ? (
        <div className="clubs-page__wrapper">
          {searchParams.get('tab') === 'map' ? (
            <HomeMap markers={markers} className="clubs-page__map" />
          ) : (
            <ClubsItems items={clubs} />
          )}
        </div>
      ) : null}

      <TickerReact className="clubs-page__ticker d-md-none" />
    </div>
  )
}

export default Clubs
