import React from 'react'
import './Button.scss'
import './ButtonArrow.scss'
import { BaseSvg } from '../index'
import { Link } from 'react-router-dom'

interface IProps {
  children?: React.ReactNode
  className?: string
  color?: '_purple' | '_green'
  link?: string
  onClick?: () => void
}

const ButtonArrow = ({
  children,
  className,
  color = '_purple',
  onClick,
  link,
  ...props
}: IProps) => {
  return (
    <>
      {link ? (
        <Link
          to={link}
          className={`button-arrow ${className ? className : ''} ${color} ${
            className ? className : ''
          }`}
        >
          {children}
          <BaseSvg name="ico-arrow-right-short" />
        </Link>
      ) : (
        <button
          className={`button-arrow ${className ? className : ''} ${color} ${
            className ? className : ''
          }`}
          onClick={onClick}
          {...props}
        >
          {children}
          <BaseSvg name="ico-arrow-right-short" />
        </button>
      )}
    </>
  )
}

export default ButtonArrow
