import React, { FC } from 'react'
import { BaseSvg } from '../../index'
import './Reviews.scss'
import { Swiper, SwiperSlide } from 'swiper/react'
import SwiperCore, { Navigation } from 'swiper'
import { IFeedback } from '../../../models/Feedback'

SwiperCore.use([Navigation])

interface IProps {
  feedback: IFeedback[]
}

const Reviews: FC<IProps> = (props) => {
  return (
    <section className="reviews-home">
      <div className="reviews-home__body">
        <BaseSvg className="reviews-home__quotes" name="ico-quotes" />
        <Swiper
          className="reviews-home__slider"
          navigation={{
            nextEl: '.reviews-home__btn-next',
            prevEl: '.reviews-home__btn-prev',
          }}
        >
          {props.feedback.map((item) => (
            <SwiperSlide key={item.id} className="reviews-home__slide">
              <div className="reviews-home__slide-top">
                <span>{item.name},</span>
                {item.club.address}
              </div>
              <div className="reviews-home__slide-text">{item.feedback}</div>
            </SwiperSlide>
          ))}
        </Swiper>
        <div className="reviews-home__buttons">
          <button className="reviews-home__btn reviews-home__btn-prev">
            <BaseSvg name="ico-arrow-slider" />
          </button>
          <button className="reviews-home__btn reviews-home__btn-next">
            <BaseSvg name="ico-arrow-slider" />
          </button>
        </div>
      </div>
    </section>
  )
}

export default Reviews
