import React, { FC, useEffect, useRef, useState } from 'react'
import Promo from '../../components/Promo/Promo'
import BreadCrumbs from '../../components/BreadCrumbs/BreadCrumbs'
import { IBreadCrumbs } from '../../models/BreadCrumbs'
import {
  BaseBorderMobile,
  ClubListTitle,
  ClubPlans,
  ClubSchedule,
  ClubServices,
  ClubSlider,
  ClubSportBar,
  ClubTop,
  ClubTrainers,
  Container,
} from '../../components'
import './Club.scss'
import ClubPrice from '../../components/Club/ClubPrice/ClubPrice'
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from 'react-router-dom'
import { useAppDispatch, useAppSelector } from '../../hooks/redux'
import { fetchClub } from '../../store/slices/сlub'
import { RootState } from '../../store'
import { ISchedule } from '../../models/Clubs'
import { Button } from '../../components'

export const ScheduleContext = React.createContext<ISchedule | null>(null)

const Club: FC = () => {
  const [breadCrumbs, setBreadCrumbs] = useState<IBreadCrumbs[]>([
    {
      id: 1,
      name: 'Наши клубы',
      link: '/clubs',
    },
  ])
  const [searchParams] = useSearchParams()
  const navigate = useNavigate()
  const { pathname } = useLocation()
  const { id } = useParams()
  const dispatch = useAppDispatch()
  const { club } = useAppSelector((state: RootState) => state.club)
  const priceScheduleBlock = useRef<null | HTMLDivElement>(null)

  useEffect(() => {
    dispatch(fetchClub(id))
  }, [])

  useEffect(() => {
    if (club) {
      setBreadCrumbs((prevState: IBreadCrumbs[]) => [
        prevState[0],
        { id: 2, name: `PULSE GYM ${club.address}` },
      ])
    }
  }, [club])

  const callBack = () => {
    navigate({
      pathname,
      search: `?tab=schedule`,
    })

    setTimeout(() => {
      const top = priceScheduleBlock.current?.offsetTop
      window.scrollTo({
        top,
        behavior: 'smooth',
      })
    }, 10)
  }

  return (
    <>
      {club && (
        <div className="club-page">
          <BaseBorderMobile />
          <ClubSlider media={club.media} className="d-none d-md-block" />

          <Promo title={club.address}>
            <ClubListTitle />
          </Promo>

          <BreadCrumbs dataList={breadCrumbs} />

          <div className="d-none d-md-block">
            <Container>
              <div className="club-page__title">pulse gym {club.address}</div>
              <ClubListTitle />
            </Container>
          </div>

          <ClubTop
            address={club.address}
            lat={Number(club.lat)}
            lng={Number(club.lng)}
            phone={club.phone}
            name={club.name}
            scheduler={club.scheduler}
          />
          <ClubSlider media={club.media} className="d-md-none" />
          <ClubServices services={club.services} />

          <div
            ref={priceScheduleBlock}
            className="club-page__wrapper d-md-none"
          >
            {searchParams.get('tab') === 'schedule' ? (
              <ScheduleContext.Provider value={club.schedule}>
                <ClubSchedule link={`/clubs/${id}`} />
              </ScheduleContext.Provider>
            ) : (
              <ClubPrice
                link={`/clubs/${id}?tab=schedule`}
                price={club.price}
                callBack={callBack}
              />
            )}
          </div>

          <div className="d-none d-md-block">
            <Container>
              <div className="club-page__buttons">
                <Button
                  to={`/clubs-price/${id}`}
                  color="_green"
                  className="club-page__btn"
                >
                  прайс-лист
                </Button>
                <Button to={`/clubs-schedule/${id}`} className="club-page__btn">
                  расписание
                </Button>
              </div>
            </Container>
          </div>

          <ClubSportBar image={club.image} className="d-md-none" />
          <ClubTrainers coaches={club.coaches} />
          <ClubSportBar image={club.image} className="d-none d-md-block" />
          <ClubPlans />
        </div>
      )}
    </>
  )
}

export default Club
