import React, { useEffect, useState } from 'react'
import {
  HomeBlog,
  HomeClubs,
  HomeCorporate,
  HomeMap,
  HomeReviews,
  HomeSmartFit,
  HomeWelcome,
  Ticker,
} from '../../components'
import { useAppDispatch, useAppSelector } from '../../hooks/redux'
import { RootState } from '../../store'
import { IClubs } from '../../models/Clubs'
import { fetchClubs } from '../../store/slices/сlubs'
import { fetchFeedback } from '../../store/slices/feedback'
import { fetchBlogs } from '../../store/slices/blogs'

const Index = () => {
  const dispatch = useAppDispatch()
  const { clubs } = useAppSelector((state: RootState) => state.clubs)
  const { data: feedback } = useAppSelector(
    (state: RootState) => state.feedback
  )
  const { data: blogs } = useAppSelector((state: RootState) => state.blogs)
  const [markers, setMarkers] = useState([])

  useEffect(() => {
    if (!clubs.length) return
    const result: any = []
    clubs.forEach((el: IClubs) => {
      result.push({
        id: el.id,
        title: el.name,
        address: el.address,
        position: {
          lat: +el.lat,
          lng: +el.lng,
        },
      })
    })
    setMarkers(result)
  }, [clubs])

  useEffect(() => {
    dispatch(fetchClubs(1))
    dispatch(fetchFeedback(1))

    const page = 1
    const tag = ''
    dispatch(fetchBlogs({ page, tag }))
  }, [])

  return (
    <div className="home-page">
      <HomeWelcome />
      <HomeClubs clubs={clubs} />
      {markers?.length ? (
        <HomeMap markers={markers} className="d-md-none" />
      ) : null}
      {clubs?.length ? <HomeSmartFit clubs={clubs} /> : null}
      <HomeCorporate />
      <Ticker />
      {feedback?.length ? <HomeReviews feedback={feedback} /> : null}
      {blogs?.length ? <HomeBlog blogs={blogs} /> : null}
    </div>
  )
}

export default Index
